@import url(https://fonts.googleapis.com/css?family=Lato|Lobster);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Lobster', cursive;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-position: right 50%;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
    padding: 0.5em;
    padding-right: 1.5em;
    border-radius: 0;
}

.app {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto 50px; }

.app-logo {
  text-decoration: none;
  color: #EB1933;
  font-size: 2.5rem; }

.app-header__heading {
  margin: 0 0 30px; }

.title {
  font-size: 2rem;
  width: 100%; }

.cart-toggle-btn {
  border: 0;
  background-color: #2151A1;
  font-size: 1rem;
  text-align: center;
  padding: 12px;
  cursor: pointer;
  color: #ffffff;
  transition: all 0.3s ease-in-out; }
  .cart-toggle-btn:hover {
    opacity: 0.9; }

.cart-items-list {
  max-width: 800px; }

@media screen and (min-width: 768px) {
  .title {
    width: auto;
    margin-right: 20px; } }

.cart__summary {
  color: #000000;
  font-size: 1.25rem;
  margin-left: 80%;
  text-align: center;
  font-weight: bold; }

.cart-item {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .cart-item > div {
    text-align: center; }
  .cart-item__img {
    max-width: 80px; }
  .cart-item__name {
    flex: 1 1;
    text-decoration: none;
    color: #000000; }
  .cart-item__price {
    width: 20%; }
  .cart-item__qty {
    width: 10%; }
  .cart-item__form {
    width: 15%;
    display: flex;
    align-items: center;
    justify-content: center; }
  .cart-item__input {
    width: 30px;
    height: 40px;
    text-align: center; }
  .cart-item__btns {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 10px; }
  .cart-item__btn {
    border: 1px solid gray;
    background-color: #ffffff;
    font-size: 1rem;
    text-align: center;
    padding: 5px;
    cursor: pointer;
    width: 20px; }
    .cart-item__btn:nth-child(2) {
      margin-top: 5px; }

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .product-list__topbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px; }

@media screen and (min-width: 768px) {
  .product-list > .product-list-item {
    width: 30%; }
  .product-list__topbar {
    justify-content: unset; } }

@media screen and (min-width: 1024px) {
  .product-list > .product-list-item {
    width: 23%; } }

.product-list-item {
  margin-bottom: 20px;
  width: 48%;
  text-align: center; }
  .product-list-item__img {
    max-width: 100%; }
  .product-list-item__name {
    font-size: 1.5rem;
    text-decoration: none;
    display: block;
    color: #000000;
    font-family: "Lobster", cursive;
    margin-top: 10px; }
  .product-list-item__price {
    margin: 10px 0 17px; }
  .product-list-item__add-to-cart {
    border: 0;
    background-color: #2151A1;
    font-size: 0.875rem;
    text-align: center;
    padding: 12px;
    cursor: pointer;
    color: #ffffff;
    width: 100%;
    transition: all 0.3s ease-in-out; }
    .product-list-item__add-to-cart:hover {
      opacity: 0.9; }

